<template>
    <div class="box">
        <header>
            <slot name="left"></slot>
            <slot name="center"></slot>
             <div v-if="this.$store.state.obj.login==true" class="righ">
                <router-link :to="{name:'My'}">
                    <img src="../../images/120.png" alt="">
                </router-link>
                </div>
            <div v-else>
                <slot name="right"></slot>
                </div>
               
        <!-- <div class="righ" v-show="isShow==false">
            <img src="../images/120.png" alt="">
        </div> -->
       
        </header>
    </div>
</template>

<script>
export default {
    data(){
        return{
           
        }
    },
    mounted(){

       
        function  rem(){
		document.documentElement.style.fontSize=document.documentElement.clientWidth/7.5+"px";
	}
	//onresize  当屏幕的尺寸发生改变的时候会触发的事件
	rem();
  window.onresize=rem;
        if(localStorage.getItem('a')){
            this.arr = JSON.parse(localStorage.getItem("a"))
            // this.isShow = true
        }
    },
}
</script>

<style lang="less" scoped>
    .box{
        width: 100%;
        // height: 100%;
        header{
            width: 100%;
            height: 1rem;
            background: #3792E5;
            span{
                font-size: 0.3rem;
            }
            p{
                font-size: 0.3rem;
            }
        }
        .righ{
            position: relative;
            font-size: 0.3rem;
            img{
                position: absolute;
                top: 0.2rem;
                right: 0.2rem;
                width: 0.5rem;
            }
        }
    }
</style>


